@import '../Sass/index.scss';
form {
  position: relative;
}

input {
  width: 45rem;
  height: 4.5rem;
  border-radius: 22px;
  padding-left: 55px;
  background-color: $lighter-grey;
  @include whiteBorder;
  font-size: 1.75rem;
  color: $white;

  &:focus {
    @include focus;
  }
}

.clear-button {
  @include cursor;
  position: absolute;
  top: 10px;
  right: 20px;
  color: $white;
  background-color: $lighter-grey;
  border: none;
  font-size: 2rem;

  &:hover {
    @include transition;
  }

  &:focus {
    outline: none;
  }
}

.search-icon {
  position: absolute;
  top: 1px;
  left: 0.8px;
  width: 44px;
  padding: 5px;
  background-color: #8EFF6F;
  border-radius: 20px;
  @include whiteBorder;
}

.clear-icon {
  width: 15px;
}

.logo-container {
  position: relative;
  top: 0px;
  right: 0px;
}

@media screen and (max-width: 716px) {
  input {
    width: 35rem;
  }
}

@media screen and (max-width: 573px) {
  input {
    width: 25rem;
  }
}

@media screen and (max-width: 457px) {
  input {
    width: 20rem;
    font-size: 1.5rem;
  }
}

@media screen and (max-width: 385px) {
  input {
    width: 18rem;
    height: 3.5rem;
    font-size: 1.2rem;
    text-align: left;
  }

  .search-icon {
    position: absolute;
    top: 1px;
    left: 0.8px;
    width: 34px;
    padding: 5px;
    background-color: #8EFF6F;
    border-radius: 20px;
    @include whiteBorder;
  }

  .clear-icon {
    width: 10px;
    margin-bottom: 5px;
  }

  .logo-container {
    margin-top: 5px;
  }

}
