@import '../Sass/index.scss';

header {
  width: 100%;
  height: 15vh;
  background-color: $lighter-grey;
  display: flex;
  align-items: center;
}

.header-container {
  @include pageMargins;
  display: flex;
  justify-content: space-between;
}

.form-container {
  width: 50%;
  display: flex;
  justify-content: flex-end;

  &:hover {
    @include cursor;
  }
}

.logo {
  width: 127px;
}

@media screen and (max-width: 385px) {
  .logo {
    width: 80px;
  }
}
